.red {
  outline: 1px solid red;
}

:root {
  --color-B: #000;
  --color-W: #fff;
  --color-G: #40c382;
  --color-bg-page: #f2f2e6;
  --color-bg-hero-attr: #f2f2e6b3;
  --vw-mobile: 450px;
  --vw-tablet: 806px;
  --vw-desktop: 1920px;
  --vh-desktop: 1140px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: sans-serif;

  & :where(*):before, & :where(*):after {
    box-sizing: border-box;
  }
}

body {
  min-width: 100vw;
  color: var(--color-B);
  overflow-x: hidden;

  & > header {
    background-color: var(--color-W);
  }

  & > footer {
    color: var(--color-W);
    background-color: var(--color-B);
  }

  & > main {
    background-color: var(--color-bg-page);
  }

  & :where(ol, ul) {
    list-style-type: none;
  }

  & [href] {
    color: var(--color-B);
    text-decoration: none;

    &:visited {
      color: var(--color-B);
    }
  }
}

.w-full {
  width: 100%;
  padding: 1rem;

  @media only screen and (width >= 1920px) {
    & {
      padding: 2rem 3.5rem;
    }
  }
}

.w-container {
  max-width: var(--vh-desktop);
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 1rem;
}

.topmenu {
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: 1fr;
  grid-template-areas: "logo logo"
                       "menu menu";
  padding-top: 1rem;
  padding-bottom: 1rem;
  display: grid;

  & > [href] {
    grid-area: logo;
    justify-self: center;
  }

  & > ul {
    text-align: center;
    grid-area: menu;
    justify-self: center;

    & > li {
      margin-top: .5rem;
      padding-left: 1rem;
      padding-right: 1rem;
    }
  }

  @media only screen and (width >= 1920px) {
    & {
      grid-template-rows: 1fr;
      grid-template-areas: "logo menu";
    }

    & > [href] {
      justify-self: start;
    }

    & > ul {
      justify-content: center;
      align-items: center;
      display: flex;

      & li:last-of-type {
        border: 1px solid var(--color-B);
        border-width: 0 0 0 1px;
      }
    }
  }
}

.hero {
  text-align: center;
  width: 100%;
  min-height: 536px;
  color: var(--color-W);
  flex-direction: column;
  justify-content: center;
  padding: 1rem;
  display: flex;
  position: relative;

  & > .hero-title {
    margin-bottom: 5rem;
  }

  & > .hero-attr {
    text-align: right;
    color: var(--color-B);
    background-color: var(--color-bg-hero-attr);
    padding: .51rem;
    position: absolute;
    bottom: 1rem;
    right: 1rem;
  }
}

body > footer {
  grid-template-areas: "div"
                       "ul"
                       "ol";
  place-items: center;
  line-height: 2;
  display: grid;
  padding-top: 2rem !important;
  padding-bottom: 4rem !important;

  & [href] {
    color: var(--color-W);
  }

  & > div {
    grid-area: div;

    & > img {
      margin: 0 auto 1.5rem;
      display: block;
    }

    & p {
      margin-bottom: 1.5rem;
    }
  }

  & > ul {
    text-align: center;
    grid-area: ul;

    & li {
      display: inline-block;
    }
  }

  & > ol {
    text-align: center;
    grid-area: ol;
  }

  @media only screen and (width >= 1920px) {
    & {
      grid-template-areas: "div ul"
                           "div ol";
    }

    & > div {
      justify-self: start;

      & > img {
        margin: 0 0 1.5rem;
      }
    }

    & > :where(ul, ol) {
      justify-self: end;
      display: flex;
    }

    & ul li {
      margin-left: .5rem;
    }

    & ol {
      & > :where(li:first-child, li:nth-child(2)) a {
        margin-right: 1rem;
      }

      & > :where(li:nth-child(2), li:nth-child(3)) a {
        border: 1px solid var(--color-W);
        border-width: 0 0 0 1px;
        padding-left: 1rem;
      }
    }
  }
}

:where(.btn, [type="submit"]) {
  border: 1px solid var(--color-B);
  padding: .5rem 1rem;
}

form {
  & fieldset {
    border: none;

    & label {
      margin-bottom: .5rem;
      display: block;
    }
  }

  & [name]:not([type="checkbox"]) {
    border: none;
    width: 100%;
    min-height: 2rem;
  }
}

.card {
  background-color: var(--color-W);
}

.index {
  & .hero {
    background-image: url("hero-image-home.556bc1d5.jpg");
    background-position: 50% 100%;
    background-size: cover;
  }
}
